import React, { Component } from 'react';
import ContentWrapper from '../Layout/ContentWrapper';
import { Card, CardBody, CardFooter, Collapse } from 'reactstrap';

class ApercuPartenaire extends Component {

    render() {
        const partenaire = this.props.partenaire
        const nom = this.props.partenaire.nom
        const description = this.props.partenaire.description
        const logo = this.props.partenaire.logo
        console.log(partenaire)
        
        return (
            <Card className="card-default">
                <CardBody className="text-center">
                    <img className="mb-2 img-fluid rounded-circle thumb64" src={logo} alt="Contact"/>
                    <h4>{nom}</h4>
                    <p>{description}</p>
                </CardBody>
                <CardFooter className="d-flex">
                    <div>
                        <button type="button" className="btn btn-xs btn-primary">Send message</button>
                    </div>
                    <div className="ml-auto">
                        <button type="button" className="btn btn-xs btn-secondary">Visiter</button>
                    </div>
                </CardFooter>
            </Card>
        );
    }
}

export default ApercuPartenaire;

