import React, { Component } from 'react';
import firebase from '../../../firebase';
import swal from 'sweetalert';
import classnames from 'classnames';

import {
    Row,
    Col,
    Progress,
    Card,
    CardBody,
    CardHeader,
    CardFooter,
    FormGroup,
    Input,
    TabContent,
    TabPane,
    Button,
    Nav,
    NavItem,
    NavLink, } 
from 'reactstrap';
import Scrollable from '../../Common/Scrollable';
import axios from 'axios';
import { url_notifications } from '../../../urls';
import _ from 'lodash'
import {typeActivite as type, typeActivite} from '../TypeActivite'
import { NouveauTicket, AbonnementValide, DemandeAbonnement, AchatTicket, DefaultActivite, AbonnementRejete,  } from './CheckActivite';

const checkDescription = (notification) => {
    //console.log(notification)
    const type = notification.type
    console.log(typeActivite[0].value==type)
    switch (type) {
        case typeActivite[0].value: { // nouveau ticket
            return (
                <NouveauTicket valeur = {notification}/>
            )
        }
        case typeActivite[1].value: { // achat ticket
            return(
               <AchatTicket valeur = {notification}/>
            )
        }
        case typeActivite[2].value: { //abonnement validé
            return(
               <AbonnementValide valeur = {notification}/>
            )
        }
        case typeActivite[3].value: { //abonnement refusé
            <AbonnementRejete valeur = {notification}/>
        }
        case typeActivite[4].value: { // demande d\'abonnement à un service
            return(
               <DemandeAbonnement valeur = {notification}/>
            )
        }
        default: {
            return(
                <DefaultActivite valeur = {notification}/>
            )
        }
    }
}

// ecart séparant la notification de la date actuelle
const checkTemps = (date) => {
    const today = new Date()
    const newDate = new Date(date)
    let dif = {};

    var diff = {}
    var tmp = today - newDate;
 
    tmp = Math.floor(tmp/1000);             // Nombre de secondes entre les 2 dates
    const sec = tmp % 60;                   // Extraction du nombre de secondes
    diff.sec = tmp

    tmp = Math.floor((tmp-sec)/60);    // Nombre de minutes (partie entière)
    const min = tmp % 60;
    diff.min = tmp

    tmp = Math.floor((tmp-min)/60);    // Nombre d'heures (entières)
    const hour = tmp % 24;                  // Extraction du nombre d'heures
    diff.hour = tmp 

    tmp = Math.floor((tmp-hour)/24);   // Nombre de jours restants
    diff.day = tmp
    console.log(diff)

    diff.sec < 60? (
        dif =  `${diff.sec}s`
    ): diff.min < 60? (
            dif = `${diff.min}m`
    ): diff.hour < 24? (
        dif = `${diff.hour}h`
    ):(
        dif = newDate.toUTCString()
    )

    return(dif)
}

const Activite = (props) => {
    const notification = props.value
    const type = typeActivite.find((type) => type.value == notification.type)
    const description = checkDescription(notification)
    const date = checkTemps(notification.date)
    console.log(date)

    return (
        <div className="list-group-item list-group-item-action">
            <div className="media">
                <span className={`${type.icon} fa-2x mr-5`} />
                <div className="media-body text-truncate">
                    <p className="mb-1">
                        <strong className="text-bold">
                            <span> {type.label} </span>
                        </strong>
                    </p>
                    <p className="mb-1 text-sm">{description}</p>
                </div>
                <div className="ml-auto">
                    <small className="text-muted ml-2"> {date} </small>
                </div>
            </div>
        </div>
    )
}

class ActiviteService extends Component {
    constructor(props) {
        super(props)
        this.state = {
            notifs: [],
            isLoading: true,
        }
    }

    componentDidMount() {
        const service = this.props.service
        const key = this.props.service.key
        console.log(service.key)

        axios.get(`${url_notifications}services/${key}`)
        .then((res) => {
            const notifs = res.data.data
            this.setState({
                notifs,
                isLoading: false,
            })
        })
        .catch((error) => {
            console.log(error.message)
            swal('Erreur','Désolé une erreur est survenue', 'warning')
        })

    }

    render() {
        const notifs = this.state.notifs
        const isLoading = this.state.isLoading

        return(
            !isLoading? (
                <div className="pt-3 mb-3">
                    {/* Activity messages */}
                    <div className="card card-default">
                        {/* START list group */}
                        <Scrollable height="300" className="list-group">
                            {notifs.map((notification) => {
                                return ( 
                                    <Activite value={notification}/> 
                                )
                            })}              
                        </Scrollable>
                        {/* END list group */}
                        {/* START card footer */}
                        <div className="card-footer">
                            <div className="input-group">
                                <input className="form-control form-control-sm" type="text" placeholder="Search message .."/>
                                <span className="input-group-btn">
                                    <button className="btn btn-secondary btn-sm" type="submit">
                                        <i className="fa fa-search"></i>
                                    </button>
                                </span>
                            </div>
                        </div>
                        {/* END card-footer */}
                    </div>
                    {/* End Activity messages */}
                </div>
            ):(
                <div> en cours de chargement... </div>
            )
            
        )
    }
}

export default ActiviteService;