import React, { Component } from 'react';
import axios from 'axios';
import {
    Row,
    Col, 
} 
from 'reactstrap';
import ApercuPartenaire from './ApercuPartenaire'

/**Affiche les informations générales d'une machine */
class InfoService extends Component {
    constructor(props) {
        super(props)

        this.state = {
            partenaires : [],
            isLoading : true,
        }
    }

    render() {
        const service = this.props.service
        const nom = this.props.service.nom
        const description = this.props.service.description
        const logo = this.props.service.logo// nombre de bouteilles que ce service a permit de recycler
        const partenaires = this.props.service.partenaires
        console.log(partenaires.length)
        return (
            <div className="">
                {/* Info machine */}
                <div className="card b">
                        {/* Nom machine */}
                        <div className="card-header">
                            <div className="mt-2">
                                <img className="rounded-circle thumb64 mb-2" src={logo} alt="User"/>
                            </div>
                            <h4 className="my-2">
                                <span> {nom}  </span>
                            </h4>
                            <small> {description} </small>
                        </div>
                        {/*Partenaires */}
                        <div className="card-body">
                            <h4 className="bg-gray-dark pl-2"> <em className="icon-tag"/> Partenaires </h4>
                            <Row>
                                {partenaires? (
                                    partenaires.map((partenaire) => {
                                        <Col lg="4" sm="6">
                                            <ApercuPartenaire partenaire = {partenaire}/>
                                            <span> Just for test</span>
                                        </Col>
                                    })
                                ):(
                                    <span> Aucun partenaire n'est abonné à ce service pour l'instant </span>
                                )}
                                    
                            </Row>
                        </div>
                </div>
                {/* End Main card */}
            </div>
        )
    }
}

export default InfoService;