/**affiche les informations d'une machine */
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import swal from 'sweetalert';
import classnames from 'classnames';

import {
    Row,
    Col,
    Progress,
    Card,
    CardBody,
    CardHeader,
    CardFooter,
    FormGroup,
    Input,
    TabContent,
    TabPane,
    Button,
    Nav,
    NavItem,
    NavLink, } 
from 'reactstrap';
import ContentWrapper from '../Layout/ContentWrapper';
import {url_categories} from '../../../urls'
import HeadingMenu from '../Layout/HeadingMenu';
import InfoService from './Info_Service';
import ActiviteService from './ActiviteService';
import StatService from '../Statistiques/StatService';

class ShowService extends Component {
    constructor(props) {
        super(props);
        this.state = {
        service: {},
        key: '',
        path: [
            { 
                label: 'Home',
                value: '/dashboard_client',
            },
            { 
                label: 'Service',
                value: '/list_service',
            },
            { 
                label: 'Visualiser',
                value: '',
            }
        ],
        isLoading: true,
        activeStep:'1',
        progressValue: [100, 0, 0],
        };
    }

    componentDidMount() {
        const id = this.props.match.params.id
        console.log(id);

        // with cloud functions
        axios.get(`${url_categories}${id}`)
        .then((res) => {
            const service = res.data.data
            console.log(res.data.data)
            const key = service.key
            console.log(res)
            this.setState({
                service,
                key,
                isLoading: false
            });
            //heading nav
            this.state.path.push({
                label: this.state.service.nom,
                value:''            
            })
        })
        .catch((error) => {
            console.log("No such service!", error);
            swal("Erreur", `Cher GreenManager, veillez selectionner un service pour obtenir ses informations ${error}`, "warning").then((value) => {
                this.props.history.push("/dashboard_client/")
            })
        })
    }

    delete(id){
        swal({
            title: 'Etes-vous sûre?',
            text: 'La suppression de ce service est une opération irréversible!',
            icon: 'warning',
            buttons: true,
            dangerMode: true,
        })
        .then((willDelete) => {
            if (willDelete) {
                axios.delete(`${url_categories}${id}`)
                .then((res) => {
                    console.log("Document successfully deleted!");
                    swal("Supprimée!", "Ce service a bien été supprimée", "success").then((value) => {
                        this.props.history.push("/list_service/")
                    })
                })
                .catch((error) => {
                    console.error("Error removing document: ", error);
                    swal("Desolée!", "Ce service n'a pu être supprimé, veuillez vérifier vos paramètres de connexion", "success")
                });
            }
        });

    }

    toggleStep = activeStep => () => {
        const progessValue1 = [100, 0, 0];
        const progessValue2 = [0, 100, 0];
        const progessValue3 = [0, 0, 100];
        
        if (this.state.activeStep !== activeStep) {
            this.setState({
                activeStep : activeStep
            });
        }
        switch (activeStep) {
            case '1':
                this.setState({
                    progressValue: progessValue1
                });
                break;
            case '2':
                this.setState({
                    progressValue: progessValue2
                });
                break;
            case '3':
                this.setState({
                    progressValue: progessValue3
                });
                break;
            default:
                console.log('default')
        }
    }

    
    render() {
        console.log(this.state.service)
        const level1 = this.state.progressValue[0]
        const level2 = this.state.progressValue[1]
        const level3 = this.state.progressValue[2]
        const colorTabHeader = "success"
        const isLoading = this.state.isLoading
        return ( 
            !isLoading? (
            <ContentWrapper>
                <HeadingMenu path={this.state.path}/>
                <div className="d-flex ml-auto col mb-4">
                    {/*updated date */}
                    <div className="mr-auto p-0">
                        <small className="mb-0 mt-1 text-dark">Mis à jour le : 04/05/21 14:24:18</small>
                    </div>
                    <div className="col-xl-2 ml-auto">
                    <Link to={`/edit_service/${this.state.key}`} title="Editer le service" className="ml-auto mr-3">
                        <Button color="success">
                            Editer
                        </Button>
                    </Link>                                    
                    <Button className="ml-auto mr-0" color="danger" onClick={this.delete.bind(this, this.state.key)}>
                        Supprimer
                    </Button>
                    </div>

                </div>
                <Card className="card-default my-1">
                    <CardHeader className="pb-0">
                        <Row>
                            <div className="col pr-0" width="20%"> <Progress color={colorTabHeader} value={level1}/> </div>
                            <div className="col p-0"> <Progress color={colorTabHeader} value={level2}/> </div>
                            <div className="col pl-0"> <Progress color={colorTabHeader} value={level3}/> </div>
                        </Row>
                    </CardHeader>
                    <CardBody className="pt-0 pb-0 pl-14 pr-14">         
                        <Nav tabs justified >
                            <NavItem>
                                <NavLink
                                tag="div"
                                className={classnames( {active: this.state.activeStep === '1'} )}
                                onClick={this.toggleStep('1')}
                                >
                                    <h4 className="text-left my-2">Informations</h4>
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink
                                tag="div"
                                className={classnames( {active: this.state.activeStep === '2'} )}
                                onClick={this.toggleStep('2')}
                                >
                                    <h4 className="text-left my-2">Activites</h4>
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink
                                tag="div"
                                className={classnames( {active: this.state.activeStep === '3'} )}
                                onClick={this.toggleStep('3')}
                                >
                                    <h4 className="text-left my-2">Statistiques</h4>
                                </NavLink>
                            </NavItem>
                        </Nav>
                    </CardBody>    
                    <TabContent activeTab={this.state.activeStep}  className="pt-0">
                        {/*information */}
                        <TabPane id="tabPane1" tabId="1">
                            <InfoService service={this.state.service}/>
                            <div className="d-flex">
                                <Button
                                className="ml-auto"
                                color="success"
                                onClick={this.toggleStep('2')}
                                >
                                    Suivant
                                </Button>
                            </div>                    
                        </TabPane>
                        {/*activite */}                        
                        <TabPane id="tabPane2" tabId="2">
                            <ActiviteService service={this.state.service}/>
                            <div className="d-flex">
                                <Button outline color="primary" onClick={this.toggleStep('1')}>
                                    Precedent
                                </Button>
                                <Button
                                className="ml-auto"
                                color="success"
                                onClick={this.toggleStep('3')}
                                >
                                    Suivant
                                </Button>
                            </div>
                        </TabPane>
                        {/*statistiques */}
                        <TabPane id="tabPane3" tabId="3">      
                            <StatService service={this.state.service}/>
                            <div className="d-flex">
                                <Button outline color="primary" onClick={this.toggleStep('3')}>
                                    Precedent
                                </Button>
                                <Button 
                                className="ml-auto"
                                color="success"
                                >         
                                    Terminer
                                </Button>
                            </div>
                        </TabPane>
                    </TabContent>                                            
                </Card>
                <Row>

                </Row>
            </ContentWrapper>
            ):(
                <div> En cours de chargement... </div>
            )
           
        );
    }
}


export default ShowService;